import React from 'react';
import coti2 from '../componentes/coti2024b.jpg';
import Slider from './slider';
import Contacto from './contacto';
//import Noticias from './noticias';
import Contactos2 from './contactos2';

function Home() {
  return (
    <div className="img-container">
      <img className="foto" src={coti2} alt="Constanza San Pedro sonriendo a la cmara"/> 
      <h1>Coti San Pedro</h1>
      <p className='texto'> 
      Mi nombre es Coti San Pedro. Nac y crec en la ciudad de Crdoba. Me form como docente e investigadora en Filosofa. Me dedico a dar clases en el nivel primario y en la Universidad Nacional de Crdoba. 
Soy militante y dirigente del Frente Patria Grande. Tengo la conviccin de que la poltica es una herramienta de transformacin social y que las nuevas generaciones debemos asumir el compromiso de cambiar la realidad. 
Quienes habitamos la provincia de Crdoba necesitamos un proyecto poltico alternativo que garantice una mejor calidad de vida para todxs.
Vivimos en un contexto social y poltico muy complejo que requiere, ms que nunca, el fortalecimiento de los lazos sociales y hacer comunidad.
Por eso quiero compartir con vos este proyecto e invitarte a que te sumes.
      </p>

      <Slider/>
  
     <Contactos2/>
     
    
    
    
     
    </div>
   
   
    
    
  );
}

export default Home;
